<style scoped>
    .scrollbar::-webkit-scrollbar {
        width: 7px;
    }
    .scrollbar::-webkit-scrollbar-button {
        background: transparent;
    }
    .scrollbar::-webkit-scrollbar-track {
        -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    }
    .scrollbar::-webkit-scrollbar-thumb {
        background-color: #949494;
        border-radius: 5px;
    }
    .names {
        text-align: left;
        font-size:28px;
        line-height: 28px; 
    }
    .messages {
        line-height: 24px; 
        text-align: left;
        overflow-y: scroll;
        height: 40vh;
        width: 45vw;
    }
    .leftbtn {
        position: absolute; 
        top: 49%; 
        left: 23%; 
        transform: translate(-50%,-50%);
    }
    .rightbtn {
        position: absolute; 
        top: 49%; 
        left:77%; 
        transform: translate(-50%,-50%);
    }
    .imgbtn {
        width: 25vh;
        cursor: pointer;
    }
    .main_card {
        min-width: 90%; 
        height: 55vh; 
        box-shadow: 3px 3px 9px #181817; 
        padding: 15px; 
        background-color: honeydew; 
        border-radius: 10px;
    }
</style>
<template>
    <div align="center" :style="'background-image: url(' + bg +');background-size: 100% 100vh;background-repeat: no-repeat; width:100%; height: 100vh;'"  >
        <div class="leftbtn">
            <!-- <input type="button" class="imgbtn" :style="'background-image:url(' + leftbtn +');'" v-on:click="go(-1)"> -->
            <img :src="defaultImgleft" class="imgbtn" v-on:click="go(-1)" @mousedown="defaultImgleft = leftbtn2"  @mouseup="defaultImgleft = leftbtn">
        </div>
        <div style="position: absolute; top: 49%; left: 50%; transform: translate(-50%,-50%);">
            <img v-if="step == -1" :src="title" style="width: 90%; box-shadow: 3px 3px 9px #181817;" v-on:click="go(1)">
            <div v-if="step >= 0 && step < bless.length" v-on:click="go(1)">
                <div class="main_card scrollbar messages">
                    <div align="center">
                        <img :src="bless[step][4]" style="min-width: 80%; max-width: 80%; box-shadow: 3px 3px 9px #181817;">
                    </div>
                    <br>
                    <div class="names">
                        <b>{{bless[step][1]}}</b>
                    </div>
                    <br>
                    <div v-html="bless[step][2]">
                    
                    </div>
                </div>
            </div>
            <img v-if="step == bless.length" :src="end" style="width: 90%; box-shadow: 3px 3px 9px #181817;" v-on:click="go(-1)">
        </div>
        <div class="rightbtn">
            <img :src="defaultImgright" class="imgbtn" v-on:click="go(1)" @mousedown="defaultImgright = rightbtn2"  @mouseup="defaultImgright = rightbtn">
        </div>
    </div>
</template>

<script>
import axios from 'axios'
axios.defaults.timeout = 6000
import {Base64} from 'js-base64'
import Speech from 'speak-tts'
import DatePicker from 'vue2-datepicker';
import 'vue2-datepicker/index.css';
import title from "../assets/img/title.jpg"
import i1 from "../assets/img/default.jpg"
import bg from "../assets/img/bg.png"
import end from "../assets/img/end.png"
import leftbtn from "../assets/img/leftbtn.png"
import rightbtn from "../assets/img/rightbtn.png"
import leftbtn2 from "../assets/img/leftbtn2.png"
import rightbtn2 from "../assets/img/rightbtn2.png"
import bksong from "../assets/voice/bksong.mp3"
export default {
    name: 'reserve',
    components: { DatePicker },
    data () {
        return {
            title: title,
            end: end,
            i1: i1,
            bg: bg,
            leftbtn: leftbtn,
            rightbtn: rightbtn,
            leftbtn2: leftbtn2,
            rightbtn2: rightbtn2,
            defaultImgleft : leftbtn,
            defaultImgright : rightbtn,
            bksong: bksong,
            audio: null,
            synth: null,
            speech: null,
            bless : [],
            basePage : 7,
            start : false,
            playing : false,
            step : -1,
        }
    },
    methods: {
        startAudio(){
            if(!this.start) {
                this.audio.play();
                this.start = true;
                this.playing = true;
            }
        }, 
        playAudio(){
            if(!this.playing) {
                this.audio.play();
                this.playing = true;
            } else {
                this.audio.pause();
                // this.audio.currentTime = 0;
                this.playing = false;
            }
        }, 
        go(num) {
            this.audio.pause();
            if(this.synth.speaking) {
                this.synth.cancel();
            }
            this.step = (this.step + num >= -1) ? (this.step + num) : -1;
            if(this.bless[this.step]) {
                if(this.bless[this.step][3]) {
                    this.audio = new Audio(this.bless[this.step][3]);
                    this.audio.volume = 0.3;
                    this.audio.loop = 'loop';
                    this.audio.play();
                } else {
                    this.synth.resume();
                    this.speak(this.bless[this.step][1] + '想對柴柴說' + this.bless[this.step][2]);
                }
            }
            
        },
        synthinit(){
            this.synth = window.speechSynthesis;
            let voices = this.synth.getVoices();
            let i = 0;
            let target = '';
            voices.forEach(element => {
                if(element.voiceURI == 'Google 國語（臺灣）') {
                    target = i;
                }
                i++;
            });
            console.log(voices);
            this.synth.voice = voices[21];
            console.log(this.synth);
        },
        speak(msg) {
            
            let u = new SpeechSynthesisUtterance();
            
            u.lang = 'zh-TW';
            u.text = msg;
            this.synth.speak(u);
        }
    },
    async mounted() {
        this.audio = new Audio(this.bksong);
        this.audio.volume = 0.3;
        this.audio.loop = 'loop';

        this.synthinit();

        let rt =  await this.$CallAPI.getGoogleSheets();
        let bex = rt.split('-,-,-,-,-\r\n');
        let ex = bex[1].split('\r\n');
        for(let inx in ex) {
            if(inx >= 0) {
                let ex2 = [];
                ex2 = ex[inx].split(',');
                console.log(ex[inx]);
                // 內文
                if(ex2[2]) {
                    ex2[2] = ex2[2].replace(/"/g, '').replace(/(?:\\[rn]|[\r\n]+)+/g, '<br>');
                }
                
                if(ex2[3] && ex2[3].indexOf('https://drive.google.com/open?id=') != -1) {
                    ex2[3] = 'https://drive.google.com/uc?export=download&id=' + ex2[3].split('https://drive.google.com/open?id=')[1];
                }
                
                if(ex2[4] && ex2[4].indexOf('https://drive.google.com/open?id=') != -1) {
                    ex2[4] = 'https://drive.google.com/uc?export=download&id=' + ex2[4].split('https://drive.google.com/open?id=')[1];
                }
                this.bless.push(ex2);
            }
        }
        console.log(this.bless);
    }
}
</script>


